.course-center-collection .course-collection-pic .center-content .center-content-center .paper-common-lh {
  margin-bottom: 20px;
}
.course-center-collection .course-collection-pic .center-content .center-content-center .paper-common-lh .paper-year {
  color: #2A283D;
  font-weight: bold;
  display: inline-block;
  text-align: center;
  height: 30px;
  line-height: 30px;
}
.course-center-collection .course-collection-pic .center-content .center-content-center .paper-common-lh .ant-btn {
  border: none;
  background: none;
  margin: 0 10px 5px 0;
  transition: none;
  background: #EFEFEF;
  border-radius: 2px;
}
.course-center-collection .course-collection-pic .center-content .center-content-center .paper-common-lh .ant-btn.ant-btn-primary {
  width: 110px;
  height: 32px;
  background: #3E78ED !important;
  border-radius: 2px;
}
.course-center-collection .course-collection-pic .center-content .center-content-center .paper-common-lh .ant-btn.ant-btn-primary span {
  color: #fff !important;
}
.course-center-collection .course-collection-pic .center-content .center-content-center .paper-common-lh .ant-btn span {
  font-size: 14px;
  color: #000;
  font-weight: 400;
}
.course-center-collection .course-collection-pic .center-content .center-content-center .paper-common-lh .ant-radio-button-wrapper {
  color: #000;
  background: none;
  border: none;
}
.course-center-collection .course-collection-pic .center-content .center-content-center .paper-common-lh .ant-radio-button-wrapper::before {
  width: 0;
}
.course-center-collection .course-collection-pic .center-content .center-content-center .paper-common-lh .ant-radio-button-wrapper.ant-radio-button-wrapper-checked {
  background: #3BB9FF;
  border-radius: 2px;
  height: 26px;
  line-height: 26px;
  color: #fff;
}
.course-center-collection .course-collection-pic .center-content .center-content-center .paper-common-lh .actives {
  color: #2cbde6;
  border-color: #2cbde6;
}
.course-center-collection .course-collection-pic .center-content .center-content-center .paper-common-lh .ant-divider-horizontal {
  margin: 16px 0;
}
.course-center-collection .course-collection-pic .center-content .center-content-center .paper-common-lh .paper-common-search {
  text-align: right;
}
.course-center-collection .course-collection-pic .center-content .center-content-center .paper-common-lh .paper-common-search .ant-input-affix-wrapper .ant-input {
  background: #F4F4F4;
  border-radius: 13.5px;
  height: 32px;
}
.course-center-collection .course-collection-pic .center-content .center-content-center .paper-common-lh .paper-common-search .paper-common-totale {
  margin-left: 20px;
  color: #888 !important;
}
.course-center-collection .course-collection-pic .center-content .center-content-center .paper-center-div {
  width: 19.75%;
  box-sizing: border-box;
  border-radius: 3px;
  margin: 0 7% 3% 0;
  cursor: pointer;
}
.course-center-collection .course-collection-pic .center-content .center-content-center .paper-center-div img {
  display: block;
  width: 100%;
  min-width: 175px;
  min-height: 105px;
  height: 213px;
}
.course-center-collection .course-collection-pic .center-content .center-content-center .paper-center-div h4 {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 120.7%;
  color: #2A283D;
  margin: 8px 0 6px 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  height: 21px;
}
.course-center-collection .course-collection-pic .center-content .center-content-center .paper-center-div p {
  margin: 0 0 4px 0!important;
  width: 100%;
  display: block;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 20px;
  color: #2A283D;
  opacity: 0.8;
}
.course-center-collection .course-collection-pic .center-content .center-content-center .paper-center-div p .i {
  color: #2A283D;
  opacity: 0.8;
  margin: 0 10px;
}
.course-center-collection .course-collection-pic .center-content .center-content-center .paper-center-div p span {
  font-size: 12px;
  color: #000000;
  opacity: 0.3;
}
.course-center-collection .course-collection-pic .center-content .center-content-center .paper-center-div p span img {
  height: 10px;
  display: inline-block;
  margin-right: 10px;
  width: auto;
  min-width: auto;
  min-height: auto;
  vertical-align: initial;
}
.course-center-collection .course-collection-pic .center-content .center-content-center .paper-center-div p .span {
  padding-left: 10px;
}
.course-center-collection .course-collection-pic .center-content .center-content-center .paper-center-div p:first-child span {
  font-size: 14px;
  color: #000000;
  margin-left: 10px;
  opacity: 01;
}
.course-center-collection .course-collection-pic .center-content .center-content-center .paper-center-div:nth-child(6n) {
  margin-right: 0;
}
.course-center-collection .course-collection-pic .center-content .center-content-center .paper-center-div:hover h4 {
  color: #3E78ED;
}
html {
  overflow-x: auto;
  overflow-y: auto;
}
